@import url(https://fonts.googleapis.com/css2?family=Viaoda+Libre&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2:wght@500&family=Viaoda+Libre&display=swap);
*{
  
  margin:0;
  padding:0;
  box-sizing: border-box;
  font-family: 'Baloo Tammudu 2', cursive;
}
.nav-utem{
  text-decoration: none;
}
.nav-link{
  text-decoration: none;
}
.navbar{
  background-color:white !important;
}
.navbar a:hover{
  color:blue !important;
}

.navbar-brand{
  font-size: 1.9rem !important;
  color:#565387 !important;
}
.menu_active{
  font-weight:bold;
  color:#3498db;
  border-bottom:1px solid black;
    
  }
#header {
   width: 100%;
   height: 80vh;
   margin-top: 70px;
   }
   #header h1 {
     margin: 0 0 10px 0;
     font-size: 48px;
     font-weight: 700;
     color: black;
    }
    #header .brand-name {
       color: #3498db;
     }
     #header h2{
       color:#484848;
       font-size: 24px;
     }
   #header .btn-get-started {
    font-weight: 500;
    font-size: 16px;
    letter-spacing: 1px;
    display: inline-block;
    padding: 10px 30px;
    border-radius: 50px;
    transition: 0.5s;
    color: #3498db;
    border: 2px solid #3498db;
    text-decoration: none;
   }
    
   #header .btn-get-started:hover {
    background: #3498db;
    color: #fff;
    }
    #header .animated{
      animation:up-down 2s ease-in-out infinite alternate-reverse both;
    }
    @-webkit-keyframes up-down {
      0% {
      -webkit-transform: translateY(10px);
              transform: translateY(10px);
      }
      100% {
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
      }
      }
      @keyframes up-down {
      0% {
      -webkit-transform: translateY( 10px);
              transform: translateY( 10px);
      }
      100% {
      -webkit-transform: translateY(-10px);
              transform: translateY(-10px);
      }
    }

    .contact_div{
      width:100%;
      height:75vh
    }

    footer p{
      padding: 0;
      margin:0;
    }
    @media (max-width: 991px) {
      #header {
      height: 80vh;
      text-align: center;
      }
      #header .header-img {
      text-align: center;
      }
      #header .header-img img {
      width: 60%;
      }
      }
      @media (max-width: 768px) {
        #header {
        margin-top: 20px;
        }
        #header h1 {
        font-size: 28px;
        }
        #header h2 {
        font-size: 18px;
        margin-bottom: 30px;
        }
        #header .header-img img {
          width: 70%;
        }
      }
